.App {
  background-color: #14104B;
}

@font-face {
  font-family: 'bree_serifregular';
  src: url('fonts/breeserif-regular-webfont.woff2') format('woff2'),
       url('fonts/breeserif-regular-webfont.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Open Sans';
  src: url('fonts/open-sans-light-webfont.woff2') format('woff2'),
      url('fonts/open-sans-light-webfont.woff') format('woff');
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

.header {
  background-color: #14104B;
  height: 100vh;
  width: 100%;
  text-align: center;
  transform: skewY(5deg)
}

.header-content {
  margin: 0 auto;
	transform: skewY(-5deg);
  height: 100%;
}

.logo {
  position: absolute;
  margin: 24px;
  left: 0;
  top: 0;  
  width: 50px;
}

.link-container {
  position: absolute;
  display: flex;
  flex-direction: row-reverse;
  align-items: flex-start;
  right: 0;
  font-size: 30px;
  margin: 24px;
}

.link {
  color: #FFF;
  font-family: bree_serifregular;
  margin-right: 16px;
  flex: 0 1 auto;
  cursor: pointer;
}

.header-centered-content {

}

.header-centered-content-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.header-title {
  font-family: bree_serifregular;
  color: #FFF;
  font-size: 10vw;
  margin: 0;
}

.header-image {
  width: 300px;
}

.header-subtitle {
  font-family: bree_serifregular;
  color: #FFF;
  font-size: 4.5vw;
  margin: 0;
}

.section-header {
  font-family: bree_serifregular;
  font-size: 40px;
  margin: 0 0 24px 0;
}

.section-content {
  max-width: 69em;
	margin: 0 auto;
	transform: skewY(-5deg);
  padding-top: 50px;
  padding-bottom: 100px;
}

.about {
  background-color: #D8D8D8;
  transform: skewY(5deg)
}

.about-container {
  display: flex;
  justify-content: center;
}

.about-item {
  background: #FFF;
  flex: 0 1 auto;
  width: 100%;
  border-radius: 20px;
}

.about-divider {
  width: 30px;
}

.about-title-container {
  display: flex;
  justify-content: center;
  padding: 24px 16px 16px 16px;
}

.about-image {
  width: 44px;
  margin-right: 8px;
}

.about-title {
  font-family: bree_serifregular;
  font-size: 30px;
  margin: 0;
}

.about-text {
  font-family: Open Sans;
  font-size: 22px;
}

.about-intro-divider {
  height: 16px;
}

.card-text {
  font-family: Open Sans;
  font-size: 20px;
  margin: 0;
  padding: 8px 16px 16px 16px;
}

.work {
  background-color: #FFF;
  transform: skewY(5deg)
}

.work-container {
  display: flex;
  align-items: flex-start;
  padding-top: 16px;
}

.work-container-reverse {
  display: flex;
  flex-direction: row-reverse;
  align-items: flex-start;
}

.work-image-item {
  width: 160px;
}

.work-image-item-small {
  display: none;
}

.work-divider {
  width: 100px;
}

.work-desc-item {
  
}

.work-title {
  font-family: bree_serifregular;
  font-size: 40px;
  margin: 0;
}

.work-text {
  font-family: Open Sans;
  font-size: 22px;
}

.articles {
  background-color: #D8D8D8;
  transform: skewY(5deg)
}

.article-container {
  display: flex;
  justify-content: center;
}

.article-item {
  background: #FFF;
  flex: 0 1 auto;
  width: 100%;
  border-radius: 20px;
  cursor: pointer;
}

.article-divider {
  width: 60px;
}

.article-image {
  margin: 0 auto;
  padding: 16px;
  width: 160px;
  display: block;
}

.article-title {
  font-family: bree_serifregular;
  font-size: 30px;
  padding: 16px 16px 0 16px;
  margin: 0;
}

.contact {
  background-color: #14104B;
  transform: skewY(5deg)
}

.contact-container {
  display: flex;
}

.contact-link {
  flex: 0 1 auto;
  width: 50%;
  display: flex;
  margin-bottom: 12px;
  cursor: pointer;
}

.contact-icon {
  height: 50px;
}

.contact-text {
  color: #FFF;
  font-family: bree_serifregular;
  font-size: 25px;
  margin-top: 4px;
  margin-left: 16px;
}

.footer {
  text-align: center;
  background-color: #0E0B36;
  transform: skewY(0deg);
}

.footer-text {
  font-family: bree_serifregular;
  color: #FFF;
  margin: 0;
  padding-top: 40px;
  padding-bottom: 40px;
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {

} 

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {

}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {

  .section-header {
    padding-left: 16px;
  }

  .contact-container {
    padding-left: 16px;
    padding-right: 16px;
  }

  .work-image-item {
    padding-left: 16px;
    padding-right: 16px;
  }

  .about-container {
    display: flex;
    padding-left: 16px;
    padding-right: 16px;
  }

  .about-text {
    padding-left: 16px;
    padding-right: 16px;
  }

  .work-container {
    padding-right: 16px
  }

  .work-container-reverse {
    padding-left: 16px
  }

  .article-container {
    margin-left: 16px;
    margin-right: 16px;
  }

}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (max-width: 768px) {
  .section-header {
    padding-left: 16px;
  }

  .about-text {
    padding-left: 16px;
    padding-right: 16px;
  }

  .work-text {
    padding-left: 16px;
    padding-right: 16px;
  }

  .about-container {
    display: unset;
  }
  
  .about-item {
    width: calc(100% - 32px);
    margin: 16px;
  }

  .work-title {
    margin-left: 16px;
  }

  .work-image-item {
    padding-left: 16px;
    padding-right: 16px;
  }

  .article-container {
    margin-left: 16px;
    margin-right: 16px;
  }

  .contact-container {
    padding-left: 16px;
    padding-right: 16px;
  }
  
}
 
/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .section-header {
    padding-left: 16px;
  }

  .link-container {
    flex-direction: column-reverse;
    align-items: flex-end;
  }

  .link {
    margin-right: 0;
  }

  .about-text {
    padding-left: 16px;
    padding-right: 16px;
  }

  .work-text {
    padding-left: 16px;
    padding-right: 16px;
  }

  .about-container {
    display: unset;
  }
  
  .about-item {
    width: calc(100% - 32px);
    margin: 16px;
  }

  .about-intro-divider{
    height: 8px;
  }

  .work-title {
    margin-right: 16px;
    margin-left: 16px;
  }

  .work-container {
    display: unset;
  }

  .work-image-item {
    display: none;
  }

  .work-image-item-small {
    display: block;
    margin: 64px;
    width: calc(100% - 128px);
  }

  .work-container-reverse {
    display: unset;
  }

  .article-container {
    display: unset;
  }
  
  .article-item {
    width: calc(100% - 32px);
    margin: 16px;
  }

  .contact-container {
    display: unset;
  }

  .contact-link {
    padding-left: 16px;
    margin-bottom: 0;
  }

  .contact-left {
    margin-bottom: 12px;
  }

  .contact-icon {
    height: 40px;
  }
}
 
